import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './components/home/home.component';
import { AboutComponent } from './components/about/about.component';
import { WhyComponent } from './components/why/why.component';
import { TPNewsComponent } from './components/tp-news/tp-news.component';
import { GalleryComponent } from './components/gallery/gallery.component';
import { CurrentLeadersComponent } from './components/current-leaders/current-leaders.component';
import { HightlightsComponent } from './components/hightlights/hightlights.component';
import { ContactComponent } from './components/contact/contact.component';

const routes: Routes = [
  { path: 'home',  component: HomeComponent },
  { path: 'about',  component: AboutComponent },
  { path: 'why',  component: WhyComponent },
  { path: 'tpnews',  component: TPNewsComponent },
  { path: 'gallery',  component: GalleryComponent },
  { path: 'currentLeaders',  component: CurrentLeadersComponent },
  { path: 'hightlights',  component: HightlightsComponent },
  { path: 'contactś',  component: ContactComponent },
  { path: '', redirectTo: '/home', pathMatch: 'full' },
  { path: '**', component: HomeComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
